import React from "react";
import PropTypes from "prop-types";
import s from "./Profile.module.css";

function Profile({ ImgeUrl, name, tag, location, stats }) {
  return (
    <div className={s.profile}>
      <div className={s.white}>
        <div>
          <img src={ImgeUrl} alt={name} className={s.avatar} />
          <p className={s.userName}>{name}</p>
          <p className={s.gray}>@{tag}</p>
          <p className={s.gray}>{location}</p>
        </div>

        <ul className={s.userStatiList}>
          <li className={s.userStat}>
            <span>Followers </span>
            <span>{stats.followers}</span>
          </li>
          <li className={s.userStat}>
            <span>Views </span>
            <span>{stats.views}</span>
          </li>
          <li className={s.userStat}>
            <span>Likes </span>
            <span>{stats.likes}</span>
          </li>
        </ul>
      </div>
    </div>
  );
}

Profile.propTypes = {
  ImgeUrl: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  tag: PropTypes.string.isRequired,
  location: PropTypes.string.isRequired,
  stats: PropTypes.object.isRequired,
};

export default Profile;
